/*
 * 
 */
export function getUa() {
	let ua = navigator.userAgent.toLowerCase();
	if (ua.indexOf("sweetaz") > -1) {
		return 'az'
	} else if (ua.indexOf("sweetios") > -1) {
		return 'ios'
	} else {
		return 'h5'
	}
}

