/*
 * string time 2020/11/14 20:20
 * int millisecond 
 */
export function setTime(time, millisecond) {
	var thisMill = new Date(time).getTime();
	var newMill = thisMill + millisecond;
	var date = new Date(newMill);
	var year = date.getFullYear(); //
	var mon = (date.getMonth() + 1) < 0 ? '0' + date.getMonth() + 1 : date.getMonth() + 1; //
	var da = (date.getDate()) < 0 ? '0' + date.getDate() : date.getDate(); //
	var h = (date.getHours()) < 0 ? '0' + date.getHours() : date.getHours(); //
	var m = (date.getMinutes()) < 0 ? '0' + date.getMinutes() : date.getMinutes(); //
	var s = (date.getSeconds() < 0) ? '0' + date.getSeconds() : date.getSeconds(); //
	return year + '-' + mon + '-' + da + ' ' + h + ':' + m + ':' + s;
}





